@import url("https://fonts.googleapis.com/css?family=Open+Sans:regular,600,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Roboto:regular,italic,500,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Yellowtail:regular&display=swap");
:root {
  --color-primary: #274c5b;
  --color-secondary: #7eb693;
  --color-yellow: #efd372;
  --color-text-main: #525c60;
  --color-light-gray: #f9f8f8;
  --color-light-blue: #eff6f1;
  --color-white: #fff;
}

[class*=__container] {
  margin: 0 auto;
  padding: 0 0.9375rem;
  max-width: 89.375rem;
}

.text {
  color: var(--color-primary);
  font-weight: 700;
  line-height: 1.17;
  font-family: "Roboto", sans-serif;
}
.text--h1 {
  font-size: 70px;
}
.text--h2 {
  font-size: 50px;
}
.text--h3 {
  font-size: 40px;
}
.text--h4 {
  font-size: 35px;
}
.text--h5 {
  font-size: 30px;
}
.text--h6 {
  font-size: 25px;
}

.pretitle {
  margin-bottom: 8px;
  color: var(--color-secondary);
  font-size: 36px;
  font-family: "Yellowtail", cursive;
  line-height: normal;
}
.pretitle--white {
  color: #fff;
}
.pretitle--small {
  margin-top: 2px;
  margin-bottom: 0;
  font-size: 22px;
}

.button-common, .contact-form__submit, .product__button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 28px 39px;
  max-height: 79px;
  min-width: 220px;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.17;
  font-family: "Roboto", sans-serif;
  background-color: var(--color-primary);
  border-radius: 16px;
  transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.button-common svg, .contact-form__submit svg, .product__button svg {
  transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.button-common:hover, .contact-form__submit:hover, .product__button:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}
.button-common:hover svg, .contact-form__submit:hover svg, .product__button:hover svg {
  transform: scale(1.5);
}
.button-common--yellow, .product__button {
  color: var(--color-primary);
  background-color: var(--color-yellow);
}
.button-common--white {
  color: var(--color-primary);
  background-color: #fff;
  outline: 2px solid var(--color-primary);
}

.link-hover:hover {
  text-decoration: underline;
  text-underline-offset: 2px;
}

.locked {
  overflow: hidden;
  touch-action: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}
*, *:before, *:after {
  box-sizing: border-box;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  min-width: 320px;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: var(--color-text-main);
  font-size: 18px;
  line-height: 1.65;
  font-family: "Open Sans", sans-serif;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  user-select: none;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
  text-decoration: none;
  user-select: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sup {
  color: inherit;
}

address {
  font-style: normal;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  color: var(--color-primary);
  font-size: 38px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  line-height: normal;
}
.logo svg {
  margin-right: 8px;
}

.socials {
  display: flex;
  gap: 15px;
}
.socials__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background-color: var(--color-light-blue);
  border-radius: 50%;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.socials__link:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}
.socials__link:hover svg {
  transform: scale(1.5);
}
.socials__link svg {
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.product {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 335px;
  height: 483px;
  color: var(--color-primary);
  background-color: var(--color-light-gray);
  border-radius: 30px;
  transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}
.product:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
}
.product:hover .product__button-backdrop {
  opacity: 1 !important;
}
.product__tag {
  position: absolute;
  top: 30px;
  left: 30px;
  padding: 6px 12px;
  color: #fff;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.36;
  background-color: var(--color-primary);
  border-radius: 8px;
}
.product__image {
  mix-blend-mode: multiply;
  object-fit: contain;
  height: 100%;
  max-height: 324px;
}
.product__details {
  padding: 0 30px 17px 30px;
}
.product__title {
  margin-bottom: 6px;
  padding-bottom: 13px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.17;
  font-family: "Roboto", sans-serif;
  border-bottom: 1px solid #dedddd;
}
.product__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.product__price, .product__discount {
  font-weight: 700;
  line-height: 1.38;
}
.product__price--discounted {
  color: #b8b8b8;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.36;
  text-decoration: line-through;
}
.product__discount {
  margin-left: 8px;
}
.product__button-backdrop {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.4);
  opacity: 0;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.product__button:disabled {
  color: #fff;
  background-color: var(--color-primary);
}

.blogs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  column-gap: 46px;
  row-gap: 115px;
}
@media (max-width: 61.99875em) {
  .blogs {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 29.99875em) {
  .blogs {
    row-gap: 200px;
  }
}
.blogs__item {
  position: relative;
}
@media (max-width: 61.99875em) {
  .blogs__item {
    max-width: 677px;
  }
}
.blogs__item img {
  width: 100%;
  height: 100%;
  border-radius: 30px;
}
.blogs__date {
  position: absolute;
  top: 43px;
  left: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 82px;
  height: 82px;
  color: var(--color-primary);
  font-weight: 700;
  line-height: 1.1;
  font-family: "Roboto", sans-serif;
  background-color: #fff;
  border-radius: 50%;
}
@media (max-width: 85.37375em) {
  .blogs__date {
    top: 32px;
    width: 72px;
    height: 72px;
  }
}
@media (max-width: 29.99875em) {
  .blogs__date {
    top: 16px;
    left: 16px;
    width: 62px;
    height: 62px;
  }
}
.blogs__date--day {
  font-size: 25px;
}
@media (max-width: 85.37375em) {
  .blogs__date--day {
    font-size: 22px;
  }
}
@media (max-width: 29.99875em) {
  .blogs__date--day {
    font-size: 20px;
  }
}
.blogs__date--month {
  font-size: 20px;
}
@media (max-width: 85.37375em) {
  .blogs__date--month {
    font-size: 18px;
  }
}
@media (max-width: 29.99875em) {
  .blogs__date--month {
    font-size: 16px;
  }
}
.blogs__details {
  position: absolute;
  bottom: -68px;
  left: 50%;
  transform: translateX(-50%);
  padding: 46px 57px 60px 57px;
  width: 100%;
  max-width: 90.5465288035%;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0px 20px 35px rgba(166, 166, 166, 0.25);
}
@media (max-width: 85.37375em) {
  .blogs__details {
    padding: 30px;
  }
}
@media (max-width: 29.99875em) {
  .blogs__details {
    bottom: -148px;
  }
}
.blogs__details > *:not(:last-child) {
  margin-bottom: 15px;
}
.blogs__details .text--h6 {
  margin-bottom: 2px;
}
@media (max-width: 85.37375em) {
  .blogs__details .text--h6 {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
@media (max-width: 29.99875em) {
  .blogs__details .text--h6 {
    font-size: 22px;
  }
}
.blogs__author {
  display: flex;
  gap: 8px;
  align-items: center;
  color: var(--color-primary);
  line-height: normal;
}
.blogs__text {
  max-width: 465px;
}
.blogs__text > p {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
@media (max-width: 29.99875em) {
  .blogs .button-common, .blogs .contact-form__submit, .blogs .product__button {
    margin: 0 auto;
    padding: 20px 30px;
    min-width: 190px;
    max-height: 74px;
  }
}

.newsletter__container {
  padding-top: 140px;
}
@media (max-width: 29.99875em) {
  .newsletter__container {
    padding-top: 120px;
  }
}
.newsletter__wrapper {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: space-between;
  padding-inline: 71px;
  height: 323px;
  background-image: url("./../images/newsletter.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 30px;
}
@media (max-width: 85.37375em) {
  .newsletter__wrapper {
    padding-inline: 51px;
    height: 295px;
  }
}
@media (max-width: 47.99875em) {
  .newsletter__wrapper {
    flex-direction: column;
    padding-block: 40px;
    height: 388px;
  }
}
@media (max-width: 29.99875em) {
  .newsletter__wrapper {
    padding: 30px 20px;
    height: 330px;
  }
}
.newsletter__wrapper .text--h2 {
  color: #fff;
  max-width: 357px;
}
@media (max-width: 47.99875em) {
  .newsletter__wrapper .text--h2 {
    max-width: 563px;
    text-align: center;
    font-size: 45px;
  }
}
@media (max-width: 29.99875em) {
  .newsletter__wrapper .text--h2 {
    font-size: 37px;
  }
}
.newsletter__form {
  display: flex;
  gap: 6px;
}
@media (max-width: 61.99875em) {
  .newsletter__form {
    flex-direction: column;
    gap: 12px;
  }
}
@media (max-width: 47.99875em) {
  .newsletter__form {
    align-items: center;
    width: 100%;
  }
}
@media (max-width: 29.99875em) {
  .newsletter__form {
    align-items: initial;
  }
}
.newsletter__form input {
  padding-inline: 23px;
  width: 349px;
  height: 79px;
  border-radius: 16px;
  outline: none;
}
@media (max-width: 47.99875em) {
  .newsletter__form input {
    width: 100%;
  }
}
@media (max-width: 29.99875em) {
  .newsletter__form input {
    height: 69px;
  }
}
.newsletter__form input::placeholder {
  font-style: italic;
  font-family: "Roboto", sans-serif;
  opacity: 0.6;
}
.newsletter__form svg {
  display: none;
}
@media (max-width: 29.99875em) {
  .newsletter .button-common, .newsletter .contact-form__submit, .newsletter .product__button {
    padding-block: 23px;
  }
}

.page-banner {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 89.375em) {
  .page-banner {
    height: 28.125rem;
  }
}
@media (min-width: 20em) and (max-width: 89.375em) {
  @supports (height: clamp( 15.625rem , 12.0213963964rem  +  18.018018018vw , 28.125rem )) {
    .page-banner {
      height: clamp( 15.625rem , 12.0213963964rem  +  18.018018018vw , 28.125rem );
    }
  }
  @supports not (height: clamp( 15.625rem , 12.0213963964rem  +  18.018018018vw , 28.125rem )) {
    .page-banner {
      height: calc(
        15.625rem + 12.5 * (100vw - 20rem) / 69.375
      );
    }
  }
}
@media (max-width: 20em) {
  .page-banner {
    height: 15.625rem;
  }
}
.page-banner__container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
@media (min-width: 89.375em) {
  .page-banner .text--h1 {
    font-size: 4.375rem;
  }
}
@media (min-width: 20em) and (max-width: 89.375em) {
  @supports (font-size: clamp( 3.25rem , 2.9256756757rem  +  1.6216216216vw , 4.375rem )) {
    .page-banner .text--h1 {
      font-size: clamp( 3.25rem , 2.9256756757rem  +  1.6216216216vw , 4.375rem );
    }
  }
  @supports not (font-size: clamp( 3.25rem , 2.9256756757rem  +  1.6216216216vw , 4.375rem )) {
    .page-banner .text--h1 {
      font-size: calc(
        3.25rem + 1.125 * (100vw - 20rem) / 69.375
      );
    }
  }
}
@media (max-width: 20em) {
  .page-banner .text--h1 {
    font-size: 3.25rem;
  }
}

.experts {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 26px;
  row-gap: 42px;
}

.expert {
  width: 449px;
  border-radius: 30px;
}
.expert:hover .expert__details {
  background-color: #fff;
  box-shadow: 0px 29px 44px rgba(149, 149, 149, 0.25);
}
.expert img {
  width: 100%;
  border-top-right-radius: 30px;
  border-top-left-radius: 30px;
}
.expert__details {
  display: flex;
  justify-content: space-between;
  padding: 35px 28px;
  background-color: var(--color-light-gray);
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: background-color, box-shadow;
}
@media (max-width: 29.99875em) {
  .expert__details {
    flex-direction: column;
    padding: 20px;
  }
}
@media (max-width: 29.99875em) {
  .expert .text--h6 {
    font-size: 22px;
  }
}

.expert-socials {
  display: flex;
  gap: 17px;
  align-self: flex-end;
}
.expert-socials__link:hover svg {
  transform: scale(1.5);
}
.expert-socials__link svg {
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.header {
  flex-shrink: 0;
  background-color: var(--color-white);
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 70px;
  max-width: 1596px;
  padding-top: 11px;
  padding-bottom: 11px;
  min-height: 204px;
}
@media (max-width: 85.37375em) {
  .header__container {
    min-height: 154px;
  }
}
@media (max-width: 47.99875em) {
  .header__container {
    min-height: 104px;
  }
}
.header__menu-block {
  display: none;
}
@media (max-width: 85.37375em) {
  .header__menu-block {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
@media (max-width: 47.99875em) {
  .header__menu-block {
    gap: 0;
    width: calc(100% - 30px);
  }
}
@media (max-width: 47.99875em) and (max-width: 29.99875em) {
  .header__menu-block {
    width: 100%;
  }
}
@media (max-width: 47.99875em) {
  .header__menu-block div {
    margin: 0 auto;
  }
}
@media (max-width: 29.99875em) {
  .header__menu-block div {
    margin: 0;
