.menu__link, .menu-dropdown__btn {
  display: flex;
  color: var(--color-primary);
  font-size: 20px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
@media (max-width: 85.37375em) {
  .menu__link, .menu-dropdown__btn {
    justify-content: center;
    font-size: 32px;
  }
}
.menu__link:hover, .menu-dropdown__btn:hover {
  color: var(--color-secondary);
}

.menu-dropdown {
  position: relative;
  display: flex;
  justify-content: center;
  height: 100%;
}
@media (max-width: 85.37375em) {
  .menu-dropdown {
    flex-direction: column;
  }
}
.menu-dropdown__btn {
  align-items: center;
  gap: 4px;
  background-color: #fff;
}
.menu-dropdown__btn:hover svg {
  fill: var(--color-secondary);
}
.menu-dropdown__btn--active {
  color: var(--color-secondary);
}
