.cart__backdrop--active {
  opacity: 1;
  pointer-events: all;
  transition: opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0ms;
}
.cart__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
@media (max-width: 29.99875em) {
  .cart__header {
    margin-bottom: 20px;
  }
}
.cart__notification {
  padding-block: 120px;
}
.cart__notification .text--h4 {
  color: var(--color-text);
  text-align: center;
  opacity: 0.5;
}
.cart__backdrop--active + .cart {
  right: 0;
}
.cart__list {
  margin-bottom: 60px;
}
@media (max-width: 29.99875em) {
  .cart__list {
    margin-bottom: 30px;
  }
}
.cart__total {
  display: flex;
  justify-content: end;
}
.cart__total .text--h5:first-child {
  margin-right: 10px;
}

.cart-product {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 29.99875em) {
  .cart-product {
    gap: 10px;
    padding-block: 10px;
  }
}
.cart-product:not(:last-child) {
  border-bottom: 1px solid #dadada;
}
.cart-product img {
  min-width: 140px;
  height: 140px;
  object-fit: contain;
}
@media (max-width: 29.99875em) {
  .cart-product img {
    min-width: 70px;
    height: 70px;
  }
}
.cart-product .text--h6 {
  font-size: 20px;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
@media (max-width: 29.99875em) {
  .cart-product .text--h6 {
    font-size: 18px;
  }
}
.cart-product .product__price, .cart-product .product__discount {
  margin-bottom: 10px;
}
.cart-product__delete {
  margin-left: auto;
}
.cart-product__delete svg {
  pointer-events: none;
}

.product-quantity {
  display: flex;
  width: fit-content;
  border-radius: 8px;
  overflow: hidden;
}
.product-quantity__button {
  width: 40px;
  height: 40px;
  color: var(--color-primary);
  font-size: 20px;
  font-weight: 600;
  line-height: 1;
  background-color: var(--color-yellow);
}
.product-quantity__input {
  width: 50px;
  border-top: 2px solid var(--color-yellow);
  border-bottom: 2px solid var(--color-yellow);
  text-align: center;
  pointer-events: none;
}